 import css from 'styled-jsx/css';
 
 const Layout = (props) => (
    <div id="grid-container">
        <header>
            Header...
        </header>

        <main>
            {props.children}
        </main>

        <footer>
            Footer...
        </footer>

        <style jsx>
            {layoutGridStyle}
        </style>

        <style jsx global>{`
            * {
                box-sizing: border-box;
                padding: 0;
                margin: 0;
            };
        `}</style>
    </div>

);

const layoutGridStyle = css`
    #grid-container {
        display: grid;
        grid-template-rows: auto 1fr auto;
    }

    header {
        grid-row: 1;
    }

    main {
        grid-row: 2;
    }

    footer {
        grid-row: 3;
    }
`;

export default Layout;